@import url(https://fonts.googleapis.com/css2?family=Fira+Mono:wght@500&family=Patua+One&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body{
  font-family: Fira Mono, Patua One;
  background: linear-gradient(90deg, rgb(34 25 213 / 56%) 0%, rgb(28 30 213 / 49%) 33%, rgb(16 99 203 / 74%) 100%);
}


/* ==== Navbar === */

nav {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-items: center;
          align-items: center;
  padding: 20px 0px;
}

.logo {
  font-size: 1.2rem;
  color: white;
  text-transform: uppercase;
}

.nav-search {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  width: 80%;
}

.form {
  width: 40%;
  position: relative;
  margin-right: 300px;
}

.search-input {
  height: 50px;
  border-radius: 25px;
  padding-left: 10px;
  width: 100%;
  position: relative;
  background: #ffffff24;
  border: white solid 1.5px;
  color: white;
  font-size: 1.2rem;
  letter-spacing: 1px;
}

.search-input:focus {
  border-radius: 25px;
  border: 1px solid #a3a3f0;
  outline: 1px solid #3e41cc;
 }

::-webkit-input-placeholder {
  font-size: 1.2rem;
  color: white;
}

:-ms-input-placeholder {
  font-size: 1.2rem;
  color: white;
}

::placeholder {
  font-size: 1.2rem;
  color: white;
}

.btn {
  position: absolute;
  top: 25%;
  right: 20px;
  background: transparent;
  border: none;
  font-size: 1.55rem;
  color: #5661d6;
}


/* ==== Current Weather === */

.weather {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  -webkit-flex-direction: column;
          flex-direction: column;
  margin-top: 50px;
}

.city-name {
  font-size: 3rem;
  text-align: center;
  color: #3e41cc96;
}

.weather-icon {
  height: auto;
  object-fit: cover;
  width: 125px;
  border-radius: 50%;
  margin: 20px 0px;
}

.temp-f {
  font-size: 4rem;
  color: white;
}

.description {
  color: #ffffffad;
}

/* ==== Historical Weather === */

.historical-weather {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  margin-top: 100px;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}

.temp-container {
  margin: 10px 20px;
  text-align: center;
  background: #ffffff24;
  padding: 40px 15px;
  
}

.temp-date {
  font-size: 1.8rem;
  color: #3e41cc96;
}


.temp-label {
  margin: 15px 0px;
  font-size: 1.8rem;
  color: white;
}

.sunrise {
  margin: 8px 0px;
}

/* ==== Err Message === */

.err-message {
  text-align: center;
  margin-top: 15px;
  color: white;
  font-size: 1.5rem;
}
